.paginationItem {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 5px;
    font-size: 16px;
}

.activePaginationElement {
    font-weight: bold;
    font-size: 18px;
    padding: 2px;
    width: 35px;
}

.paginationItem span {
    padding: 4px;
    cursor: pointer;
    border-radius: 50%;
    width: 30px;
    text-align: center;
    display: flex;
    justify-content: center;
}

