.input {
    box-sizing: border-box;
    padding: 12px;
    border-radius: 6px;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.06);
    height: 10px;
    resize: none;
    border: 2px solid black;
    font-size: 14px;
    font-weight: 550;
    margin: 4px 0;
}

.remember {
    font-weight: bold;
    margin-top: 5px;
}

.buttonBlock {
    margin-left: 55px;
    margin-top: 10px;
}

.button {
    appearance: none;
    -webkit-appearance: none;
    padding: 8px 10px;
    border: 2px solid black;
    background-color: #0404cb;
    color: #fff;
    font-weight: 600;
    border-radius: 5px;
    width: 15%;
    cursor: pointer;
    opacity: 0.8;
}

.captchaImg {
    width: 200px !important;
    height: 200px !important;
    display: flex;
}

